/*
	POPUPS
*/
.hide {
	display: none;
}
	
.popup {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	left: 0;
	z-index: 1002;
	background: rgba(0,0,0,0.9);
	height: 100%;
	width: 100%;
	

	.inner {
		height: 100%;
		width: 100%;
		top: 0;
	}

	&_close {
		position: absolute;
		top: 0;
		right: 20px;
		font-size: 18px;
		cursor: pointer;
		width: 30px;
		height: 30px;
		border-radius: 50%;
		background-color: colour(orange);

		@media all and (max-width: $md) {
			position: fixed;
			top: 50px;
			right: 27px;
		}

		span {
	        display: block;
	        position: absolute;
	        height: 3px;
	        width: 60%;
	        background-color: colour(white);
	        border-radius: 2px;
	        left: 0;
            right: 0;
            bottom: 0;
            top: 0;
            margin: auto;
	       
	        &:first-child { @include rotate(45deg); }
	        &:last-child { @include rotate(-45deg); }
	    }
	}

	&_content {
		position: relative;
		background: colour(white);
		max-height: 93%;
		overflow-y: auto;
		margin: 2rem auto;
		
		@media all and (max-width: $md) {
			width: 94%;
			margin: 2rem 0;
		}

		a:not(.orange-button) {
			color: colour(orange);
		}
	}

	&_liner {		
		height: 100%;
		position: relative;
		padding: 3em;
	}
}


.capability_popup {
	position: relative;

	.capability {

		&_content {
			color: colour(grey);
			overflow: auto hidden;
		}

		&_name {
			font-size: 2rem;
			padding: 0 0 0.5em;
			color: colour(orange);			
		}

		&_banner {
			background: colour(orange) url('../images/rounded-square-pattern-orange.jpg') no-repeat center center;
			color: colour(white);
			margin: -3em;
			margin-top: 3em;

			&_content {
				padding: 3rem;
				text-align: center;

				h3 {
					color: colour(white);
					font-size: 2rem;
					padding: 0 0 1rem;
					
					@media all and (max-width: $md) {
						font-size: 1.4rem;
						line-height: 1.7rem;	
					}
				}

				a {
					
					color: pink;
				}
			}
		}

	}

	.tabs_related {
		position: absolute;
		margin: 4em 0 2em;

		h4 {
			font-size: 1.2rem;
			color: colour(grey);
		}

		.tabs_tabs {

			margin-bottom: 0;
			
			li {
				
				a {
					border: 2px solid colour(orange);
					color: colour(orange);

					&:hover {
						background-color: colour(orange);
						color: colour(white);
					}
				}
			}
		}
	}
}