.ResourceHolderPage {

	.resources__menu {

		.optionset > label {
			font-weight: 800;
			text-transform: uppercase;
			padding: 0 0 10px;
			/* margin-bottom: 10px;
			margin-top: 20px; */
		}

		.field.checkboxset {
			li {
				display: block;

				label::before {
					border-radius: 0;
					border: 1px solid colour(grey);
					width: 18px;
					height: 18px;
					top: 0;
				}

				label::after {
					top: 1px;
					left: 1px;
					font-size: 18px;
				}

				input:checked + label:before {
					background: colour(blue);
				}
			}
		}

		.readmore {
			margin-top: 40px;
		}
	}

	/* Change the colour of the pagination */
	.pagination {
		ul li {
			a {
				color: colour(white);
			}

			&.active a {
				color: colour(blue);
			}
		}

		.next,
		.previous {
			li a span {
		    	&::before,
		    	&::after {
		    		background-color: colour(white);
				}
		    }
		}
	}		

}