.resources {
	.resource {
		float: left;
		margin-bottom: 30px;
		background-color: colour(white);
		min-height: 450px;
		position: relative;
		padding-bottom: 80px;
	}

	.resource__image {
		width: 100%;
		height: 220px;
		@include optimiseBgImg();
	}

	.resource__type--Image {
		padding-bottom: 0;
		min-height: 530px;

		.resource__image {
			height: 530px;
		}
	}

	.resource__category {
		font-family: $headingFont;
		font-size: 11px;
		font-weight: 900;
		text-transform: uppercase;
		letter-spacing: 1px;
		color: colour(blue);
		display: inline-block;
		padding: 12px 22px;
		background-color: colour(black);
		margin: 50px 0 0;
	}

	.resource__content {
		padding: 30px 25px;

		h2 {
			font-family: $headingFont;
			font-size: 21px;
			font-weight: 900;
			letter-spacing: 1px;

			a {
				color: colour(black);
			}
		}

		p {
			font-size: 18px;
		}

		.readmore {			
			position: absolute;
			bottom: 25px;
			left: 25px;

			p {
				font-size: 14px;
			}
		}
	}
}

.resources__all {
	margin-bottom: 80px;

	.resource {
		width: 31%;
		margin-right: 3.4%;		
		
		&:nth-child(3n) {
			margin-right: 0;
		}

		&:nth-child(4n) {
			clear: left;
		}

		@media (max-width: $xl) {
			width: 48%;
			margin-right: 4%;
		
			&:nth-child(2n) {
				margin-right: 0;
			}

			&:nth-child(3) {
				margin-right: 3.4%;
				clear: left;
			}

			&:nth-child(4) {
				clear: none;
			}
		}
	}	
}

.resources__featured {

	.resource {
		width: 23.5%;
		margin-right: 2%;
		height: 450px;

		&:nth-child(3),
		&:nth-child(4n+7) {
			margin-right: 0;
		}
	}

	.resource--first {
		width: 49%;
		padding: 0;
		min-height: 530px;

		.resource__image {
			width: 48%;
			margin-right: 2%;
			min-height: 530px;
			float: left;
		}

		.resource__content {
			width: 40%;
			float: left;
			padding: 30px 5%;

			.resource__category {
				background-color: transparent;
				color: colour(light_grey);
				padding: 0 0 20px;
			}
		}

		.readmore {
			left: 55%;
		}
	}

	@media (max-width: 1330px) {
		.resource {
			width: 31%;
			margin-right: 3%;

			&:nth-child(3),
			&:nth-child(4n+7) {
				margin-right: 3%;
			}

			&:nth-child(2),
			&:nth-child(3n+5) {
				margin-right: 0;
			}
		}
	
		.resource--first {
			width: 65%;
		}
	}

	@media (max-width: 880px) {
		.resource {
			width: 48.5%;

			&:nth-child(2),
			&:nth-child(3n+5) {
				margin-right: 3%;
			}

			&:nth-child(2n+3) {
				margin-right: 0;
			}
		}
	
		.resource--first {
			width: 100%;
			margin-right: 0;
		}
	}

	@media (max-width: $md) {
		.resource {
			width: 100%;
			margin-right: 0;

			&:nth-child(2),
			&:nth-child(3n+5) {
				margin-right: 0;
			}
		}

		.resource--first {
			padding-bottom: 80px;

			.resource__image {
				width: 100%;
				float: none;
				margin: 0;
			}

			.resource__content {
				width: 90%;
				float: none;

				.resource__category {
					margin: 0;
				}
			}

			.readmore {
				left: 25px;
			}
		}
	}

}