.pagination {
	position: relative;

	ul {
		padding: 0 40px;

		&.hidden-xs {
			text-align: center;
		}

		li {
			display: inline-block;

			a {
				display: block;
				padding: 10px 20px;
				@include fontRules(nunito, 16, 800);
				color: colour(black);
			}

			&.active a {
				color: colour(blue);
			}
		}
	}

	.next,
	.previous {
		padding: 0;
		position: absolute;
		top: 0;

		li {
			margin: 0;

			a {
				position: relative;
				display: block;
				width: 30px;
			    height: 30px;
			    padding: 5px;

			    span {
			    	@include angleBracket(12,0, colour(black));	
			    	display: block;
			    	width: 32px;
			    	height: 35px;
			    	left: 0;
			    }
			}
		}
	}

	.previous {
		left: 0;
	}

	.next {
		right: 0;
	}

	.previous li a span {
		transform: rotate(180deg);
	}
}